<template>
  <div>
    <v-card>
      <v-toolbar flat>
        <v-toolbar-title>Plugins</v-toolbar-title>
        <tooltip-icon-button
          btnColor="default"
          btnIcon="fa-refresh"
          btnTooltip="Refresh"
          buttonSize="medium"
          :onClick="() => handleRefresh()"
        />
        <v-spacer></v-spacer>
        <v-btn x-small class="mr-2 btn-height-22 white--text" color="orange darken-1" @click="onOpenFFRestartDialog">
          Restart Flow Framework<v-icon x-small right> fa-repeat</v-icon>
        </v-btn>
      </v-toolbar>
      <v-data-table
        v-model="selectedItems"
        item-key="uuid"
        show-select
        :loading="loadingData"
        :search="search"
        :headers="headers"
        :items="plugins"
        :custom-filter="filterCriteria"
        class="elevation-1"
        :footer-props="tableProps.FOOTER_PROPS"
        :itemsPerPage="tableProps.ITEMS_PER_PAGE"
        dense
      >
        <template v-slot:[`item.config`]="{ item }">
          <v-icon small @click="onClickEditItem(item)" color="primary">fa-pencil</v-icon>
        </template>
        <template v-slot:[`item.enabled`]="{ item, index }">
          <switch-button
            :is-enable="item.enabled"
            enable-text="Disable"
            disable-text="Enable"
            enable-color="exportColor"
            disable-color="grey"
            :on-switch="() => handleEnablePlugin(item, index)"
          />
        </template>

        <template v-slot:[`header.enabled`]>
          {{ toUpperTextKey('enabled') }}&nbsp;
          <v-icon x-small @click="openMassEditDialog('enabled')" color="primary">fa-pencil</v-icon>&nbsp;
        </template>
      </v-data-table>
    </v-card>

    <!-- Mass Edit dialog-->
    <v-dialog v-model="dialogMassEdit" persistent width="350px">
      <plugins-mass-edit-dialog
        v-if="dialogMassEdit"
        :is-open="dialogMassEdit"
        :on-close="() => (dialogMassEdit = false)"
        :edit-all="editAll"
        :edit-field="editField"
        :on-save="saveMassEdit"
      />
    </v-dialog>

    <!-- Edit dialog-->

    <config-form :is-open="dialogConfig" :on-close="() => (dialogConfig = false)" :plugin-name="pluginName" />
    <confirm-dialog
      :is-open="isRestartFFOpen"
      :loading="isRestartingFF"
      :on-confirm="() => restartFlowFramework()"
      dialog-text="Are you sure want. This will restart Flow Framework?"
      :on-close="() => closeRestartDialog()"
    ></confirm-dialog>
  </div>
</template>
<script>
import ConfigForm from './ConfigForm';
import PageTitle from '../Common/PageTitle.vue';
import { toUpperTextKey } from '../../utils/utils';
import YamlEditor from '../Common/YamlEditor.vue';
import SwitchButton from '../Common/SwitchButton.vue';
import ConfirmDialog from '../Common/ConfirmDialog.vue';
import PluginsMassEditDialog from './PluginsMassEditDialog';
import * as appsService from '../../services/appsService';
import * as pluginService from '../../services/pluginService';
import TooltipIconButton from '../RubixApps/TooltipIconButton.vue';
import { DATA_TABLE_PROPS, SERVICE_NAMES, RUBIX_APP_STATUS_CONTROL_ACTIONS } from '../../constants/appConstant';
const UPDATE_PLUGIN_STATES = {
  DISABLED: 'disabled',
  ENABLED: 'enabled',
};

export default {
  components: {
    PageTitle,
    YamlEditor,
    TooltipIconButton,
    SwitchButton,
    PluginsMassEditDialog,
    ConfigForm,
    ConfirmDialog,
  },
  watch: {},
  data() {
    return {
      isRestartFFOpen: false,
      isRestartingFF: false,
      toUpperTextKey: toUpperTextKey,
      tableProps: { ...DATA_TABLE_PROPS },
      plugins: [],
      headers: [
        { text: 'Name', align: 'start', value: 'name', sortable: true },
        { text: 'Module Path', align: 'start', value: 'module_path' },
        { text: 'Author', align: 'start', value: 'author' },
        { text: 'Website', align: 'start', value: 'website' },
        { text: 'Enabled', align: 'start', value: 'enabled', sortable: true },
        { text: 'Protocol Type', align: 'start', value: 'protocol_type' },
        { text: 'UUID', align: 'start', value: 'uuid' },
        { text: 'Config', align: 'start', value: 'config' },
      ],
      selectedItems: [],
      editAll: { enabled: false },
      dialogMassEdit: false,
      dialogConfig: false,
      loadingData: false,
      search: '',
      searchItems: [],
      editField: '',
      pluginName: '',
    };
  },
  mounted() {
    this.getPlugins();
  },
  methods: {
    restartFlowFramework() {
      this.isRestartingFF = true;
      appsService
        .changeServiceStatus(RUBIX_APP_STATUS_CONTROL_ACTIONS.RESTART, SERVICE_NAMES.RUBIX_FLOW_FRAMEWORK)
        .then(() => {
          this.isRestartingFF = true;
          this.$toasted.show('Successfully restarted flow framework');
        })
        .catch(err => {
          this.$toasted.show('Failed to restart flow framework', { type: 'error' });
        })
        .finally(() => {
          this.isRestartingFF = false;
          this.closeRestartDialog();
        });
    },
    closeRestartDialog() {
      this.isRestartFFOpen = false;
    },
    onOpenFFRestartDialog() {
      this.isRestartFFOpen = true;
    },
    getPlugins() {
      this.loadingData = true;
      pluginService
        .fetchPlugins()
        .then(data => {
          this.plugins = data;
          this.$store.commit('changeRfPlugins', data);
        })
        .finally(() => {
          this.loadingData = false;
        });
    },

    updatePlugin(plugin) {
      const body = { enabled: plugin.enabled };
      const index = this.plugins.findIndex(n => n.uuid == plugin.uuid);

      pluginService
        .enablePluginsConfigByName(plugin.name, body)
        .then(data => {
          if (data && data.state) {
            if (data.state == UPDATE_PLUGIN_STATES.ENABLED) {
              this.plugins[index].enabled = true;
              this.$toasted.show(`Plugin enabled.`);
            } else if (data.state == UPDATE_PLUGIN_STATES.DISABLED) {
              this.plugins[index].enabled = false;
              this.$toasted.show(`Plugin disabled.`);
            }
          }
        })
        .catch(() => {
          this.plugins[index].enabled = false;
        });
    },
    saveMassEdit(value, field) {
      for (const item of this.selectedItems) {
        item[field] = value;
        this.updatePlugin(item);
      }
      this.dialogMassEdit = false;
    },
    openMassEditDialog(fieldName) {
      if (this.selectedItems.length < 1) {
        return this.$toasted.show('Please select item first!', { type: 'error' });
      }
      this.dialogMassEdit = true;
      this.editField = fieldName;
      this.editAll = { enabled: false };
    },
    onClickEditItem(item) {
      this.dialogConfig = true;
      this.pluginName = item.name;
    },
    handleEnablePlugin(plugin, index) {
      this.plugins[index].enabled = !this.plugins[index].enabled;
      this.updatePlugin(plugin);
    },
    handleRefresh() {
      this.selectedItems = [];
      this.getPlugins();
    },
    onSearchChange() {
      this.searchItems = this.search.split(',').map(val => val.trim());
    },
    filterCriteria(value, search, item) {
      return (
        value != null &&
        this.searchItems &&
        Array.isArray(this.searchItems) &&
        this.searchItems.length > 0 &&
        this.searchItems.every(val => typeof val === 'string') &&
        this.searchItems.some(searchVal => value.toString().toLowerCase().indexOf(searchVal.toLowerCase()) !== -1)
      );
    },
  },
};
</script>
