<template>
  <div>
    <page-title title="Plugins" />
    <v-tabs v-model="tab" right :color="`${$vuetify.theme.dark ? '' : 'nubeGreen'}`">
      <v-tab :key="0"> PLUGINS </v-tab>
      <v-tab :key="1"> PLUGINS INSTALLATION </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <plugins></plugins>
      </v-tab-item>
      <v-tab-item :key="1">
        <rf-flow-framework-upgrade></rf-flow-framework-upgrade>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>
<script>
import Plugins from './Plugins.vue';
import PageTitle from '../Common/PageTitle.vue';
import RfFlowFrameworkUpgrade from '../RubixFlow/RFFrameworkUpgrade/rfFrameworkUpgrade.vue';
export default {
  name: 'plugins-page',
  components: { Plugins, RfFlowFrameworkUpgrade, PageTitle },
  data: () => ({
    tab: 0,
    tabs: ['apps', 'apps-upgrade'],
  }),
  mounted() {
    const { tab: tabKey } = this.$route.query;
    this.tab = this.tabs.indexOf(tabKey);
  },
  watch: {
    '$route.query.tab': function (val) {
      this.tab = this.tabs.indexOf(val);
    },
    'tab': function (val, oldVal) {
      const { tab } = this.$route.query;
      if (tab !== this.tabs[val]) {
        const tabKey = this.tabs[val];
        this.$router.push({ path: 'plugins', query: { tab: tabKey } });
      }
    },
  },
};
</script>
