<template>
<!--  https://github.com/koca/vue-prism-editor/issues/87-->
<!--  <prism-editor class="my-editor" v-model="value" :highlight="highlighter" line-numbers></prism-editor>-->
  <prism-editor class="my-editor" v-model="value" :highlight="highlighter"></prism-editor>
</template>

<script>
import { PrismEditor } from 'vue-prism-editor';
import 'vue-prism-editor/dist/prismeditor.min.css'; // import the styles somewhere

import { highlight, languages } from 'prismjs/components/prism-core';
import 'prismjs/components/prism-clike';
import 'prismjs/components/prism-yaml';
import 'prismjs/themes/prism-tomorrow.css'; // import syntax highlighting styles

export default {
  components: {
    PrismEditor,
  },
  props: ['code'],
  data(vm) {
    return { value: vm.code };
  },
  watch: {
    value: function (newVal, oldVal) {
      if (newVal !== oldVal) {
        this.onInput(newVal);
      }
    },
  },
  methods: {
    highlighter(code) {
      return highlight(code, languages.yml); // languages.<insert language> to return html with markup
    },
    onInput(v) {
      this.$emit('input', v);
    },
  },
};
</script>

<style>
/* required class */
.my-editor {
  /* we dont use `language-` classes anymore so thats why we need to add background and text color manually */
  background: #2d2d2d;
  color: #ccc;

  /* you must provide font-family font-size line-height. Example: */
  font-family: Fira code, Fira Mono, Consolas, Menlo, Courier, monospace;
  font-size: 14px;
  line-height: 1.5;
  padding: 5px;
}

/* optional class for removing the outline */
.prism-editor__textarea:focus {
  outline: none;
}

/* remove extra top margin */
.v-application .mt-4 {
  margin-top: 0 !important;
}
</style>
