import http from '../axios';
import qs from 'query-string';

export const endpoint = 'rf-plugins';

export const fetchPlugins = query => {
  let url = endpoint;
  if (query) {
    url = url + '?' + qs.stringify(query);
  }
  return http.get(`${url}`);
};

export const fetchNetworksWithPlugins = () => {
  return fetchPlugins({ has_network: true });
};

export const fetchSchema = (modulePath, moduleName) => {
  return http.get(`${endpoint}/schema?modulePath=${modulePath}&moduleName=${moduleName}`);
};

export const fetchPluginsConfigByName = pluginName => {
  return http.get(`${endpoint}/config/${pluginName}`);
};

export const enablePluginsConfigByName = (pluginName, body) => {
  return http.post(`${endpoint}/enable/${pluginName}`, body);
};

export const patchPluginsConfigByName = (pluginName, body) => {
  return http.post(`${endpoint}/config/${pluginName}`, body);
};
